
  button {
   margin: 30px;
   font-size: 18px;
   height: 65px;
   width: 250px;
   border-radius: 10px;
   border: none;
   box-shadow: 1px 1px 0px 2px rgba (0,0,0,0.3);
   background: rgb(141,217,252);
    cursor: pointer;
  }
  
  .red {
    background: red;
  }
  
  .green {
    background: green;
  }
  
  .yellow {
    background: yellow;
  }